<template>
  <div>
    <div class="news">
      <p class="title">头像设置</p>
      <div class="content">
        <div>
          <div class="ts">
            <img
              style
              :src="formData.photo == '' ? imgUrl + userInfo.photo : imgUrl + formData.photo"
              alt
            />
            <p style="color: #2868d1;">{{formData.photo == '' ? '*点击按钮上传新头像' : '*已添加,想更换直接上传新头像即可'}}</p>
          </div>
          <br />
          <br />
          <Upload
            accept="image/*"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="photo"
            :show-upload-list="false"
          >
            <Button icon="ios-cloud-upload-outline">选择新的头像</Button>
          </Upload>
        </div>
        <br />
        <br />
        <br />
        <Button @click="submit" type="primary">确认提交新头像</Button>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, Upload } from "iview";
import { changePhoto } from "../../../api/user";
import { messages, fileUploadUrl, imgUrl } from "../../../utils";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    [Button.name]: Button,
    [Upload.name]: Upload
  },
  computed: {
    ...mapState("user", ["userInfo"]),
    ...mapState("app", ["token"])
  },
  data() {
    return {
      imgUrl,
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: null
        }
      },
      formData: {
        photo: ""
      }
    };
  },
  methods: {
    ...mapActions("user", ["getUserInfo"]),
    /**
     * @function 企业营业执照
     */
    photo(res) {
      if (res.status == "SUCCESS") {
        this.formData.photo = res.data.url;
      }
    },
    submit() {
      if (this.formData.photo != "") {
        changePhoto(this.formData).then(res => {
          if (res.data.status == "SUCCESS") {
            this.getUserInfo().then(() => {
              messages.success("头像修改成功");
              this.$router.push("/user/set-index");
            });
          }
        });
      } else {
        messages.error(`还没有上传新头像`);
      }
    }
  },
  created() {
    this.fileconfig.fileHead.token = this.token;
  }
};
</script>

<style lang="less" scoped>
.news {
  width: 100%;
  background: #ffffff;
  padding: 20px 0px;
  .title {
    border-left: 5px solid #575757;
    padding-left: 10px;
    color: #2868d1;
    font-size: 16px;
    margin-left: 20px;
    right: 0;
  }
  .content {
    padding: 50px 100px;
    min-height: 425px;
    text-align: center;

    .ts {
      img {
        width: 100px;
        height: 100px;
        margin: 20px 0px;
        border-radius: 50%;
      }
    }
    span {
      width: 100px;
      display: block;
      float: left;
      margin-bottom: 20px;
      line-height: 20px;
    }
  }
}
</style>

